<template>
  <div>
    <h4>历次全国普查人口数据</h4>
    <div ref="myBar" class="barStyle"></div>
  </div>
</template>

<script>
import axios from "axios";
import * as echarts from "echarts";

export default {
  data() {
    return {
      myBar: null,
      cate_data: [],
      val_data: [],
      data: {},
    };
  },

  mounted() {
    this.myBar = this.$refs.myBar;

    let getBarData = async () => {
      let barChart = echarts.init(this.myBar);
      barChart.showLoading(); // 显示加载动画
      let barData = await axios({
        url:
          "https://www.fastmock.site/mock/fd2e7424a07c513608ee1f587b8f58fd/api/bar",
      });
      this.data = barData.data.bar;
      barChart.hideLoading(); // 隐藏加载动画
    };

    let setData = () => {
      this.cate_data = this.data.map((v) => v.title);
      this.val_data = this.data.map((v) => v.num);
    };

    getBarData().then(() => {
      setData();
      let bar = echarts.init(this.myBar);
      let option = {
        xAxis: {
          type: "value",
          axisLabel: {
            color: "white",
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            z: 0,
            lineStyle: {
              width: 10,
              color: "#2D3443",
            },
          },
        },
        yAxis: {
          type: "category",
          data: this.cate_data,
          axisLabel: {
            color: "white",
          },
        },
        grid: {
          top: "5%",
          bottom: "2%",
          left: "6%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        series: [
          {
            type: "bar",
            data: this.val_data,
            label: {
              show: true,
            },
            itemStyle: {
              borderRadius: [0, 20, 20, 0],
              color: new echarts.graphic.LinearGradient(
                0, 0, 1, 1,
                [
                  {
                    offset: 0,
                    color: '#FFB5C2'
                  },
                  {
                    offset: 0.5,
                    color: '#FF9AA2'
                  },
                  {
                    offset: 1,
                    color: '#FED8B1'
                  }
                ]
              )
            },
            animationDelay: function (idx) {
              return idx * 50;
            }
          },
        ],
      };
      bar.setOption(option);
    });
  },
};
</script>

<style lang="scss" scoped>
.barStyle {
  width: 100%;
  height: 23rem;
}
</style>