<template>
  <div>
    <!-- 图的标题 -->
    <h4>历次普查受教育程度人口</h4>
    <div ref="myLine" class="lineStyle"></div>
  </div>
</template>

<script>
import axios from "axios";
import * as echarts from "echarts";

export default {
  data() {
    return {
      myLine: null, // ref 类型的变量，初始值为 null，用于存储 echarts 实例
      data: {}, //保存从后端获取到的折线图数据
      num: {}, //保存从 data 中提取出来的数值型数据
    };
  },

  methods: {
    getLineData: function () {
      const that = this;
      axios
        .get(
          "https://www.fastmock.site/mock/fd2e7424a07c513608ee1f587b8f58fd/api/line"
        )
        .then(function (res) {
          that.data = res.data.chartData;
          that.setData();
          that.renderChart();
        });
    },

    setData: function () {
      //从 data 中提取出数值型数据
      this.num = this.data.num;
    },

    renderChart: function () {
      const that = this;
      //组件挂载后执行，使用echarts.init()方法将DOM的div元素myLine转换为图表实例
      this.$nextTick(function () {
        that.myLine = this.$refs.myLine;
        let lineChart = echarts.init(that.myLine);
        let option = {
          xAxis: {
            type: "category", //坐标轴类型为类目轴
            data: that.data.date, //年份
            boundaryGap: false, //坐标轴两端不留白
            axisLabel: {
              //坐标轴标签的样式
              color: "white",
              fontSize: 12,
              fontWeight: 500,
            },
            axisLine: {
              lineStyle: {
                color: "#ff7f50",
              },
            },
            axisTick: {
              show: false,
            },
          },
          yAxis: {
            type: "value", //纵坐标轴的值是数值类型
            axisLabel: {
              color: "white",
              fontSize: 12,
              fontWeight: 500,
            },
            axisLine: {
              lineStyle: {
                color: "#ff7f50",
              },
            },
            splitLine: {
              show: false,
            },
          },
          grid: {
            //图的显示位置与页面的布局关系
            left: "13%",
            containLabel: true, //距离包含坐标轴上的文字
            bottom: "2%",
          },
          series: [
            {
              type: "line",
              name: "65+",
              data: that.num.over_sixtyfive, //60+人口的数据
              smooth: true, //采用贝塞尔曲线来平滑绘制折线
              showSymbol: true, //显示折点标记
              stack: "total", // 数据系列的堆叠顺序为"total"
              lineStyle: {
                //不显示折线
                width: 0,
              },
              emphasis: {
                //鼠标悬停在该数据系列上时，该系列会变为高亮状态。
                focus: "series",
              },
              areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  //颜色渐变的方向
                  {
                    //不同百分比下颜色的值
                    offset: 0,
                    color: "#ff9a9e",
                  },
                  {
                    offset: 1,
                    color: "#fad0c4",
                  },
                ]),
              },
            },
            {
              type: "line",
              name: "60-64",
              data: that.num.sixty_sixtyfour, //60-64人口的数据
              smooth: true,
              showSymbol: true,
              stack: "total",
              lineStyle: {
                width: 0,
              },
              emphasis: {
                focus: "series",
              },
              areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: "#00F260",
                  },
                  {
                    offset: 1,
                    color: "#0575E6",
                  },
                ]),
              },
            },
            {
              type: "line",
              name: "15-59", //15-59人口的数据
              data: that.num.fifteen_fiftynine,
              smooth: true,
              showSymbol: true,
              stack: "total",
              lineStyle: {
                width: 0,
              },
              emphasis: {
                focus: "series",
              },
              areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: "#43C6AC",
                  },
                  {
                    offset: 1,
                    color: "#191654",
                  },
                ]),
              },
            },
            {
              type: "line",
              name: "0-14", //0-14人口的数据
              data: that.num.zero_fourteen,
              smooth: true,
              showSymbol: true,
              stack: "total",
              lineStyle: {
                width: 0,
              },
              emphasis: {
                focus: "series",
              },
              areaStyle: {
                opacity: 0.8,
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: "#8E2DE2",
                  },
                  {
                    offset: 1,
                    color: "#4A00E0",
                  },
                ]),
              },
            },
          ],
          tooltip: {
            show: true,
            trigger: "axis", //鼠标悬停到坐标轴上时触发提示框
            axisPointer: {
              type: "cross", //指示器样式为交叉十字形
            },
            formatter: function (params) {
              // 配置鼠标悬浮到数据点上的提示内容
              let index = params[0].dataIndex; //获取当前鼠标悬停的数据点的索引
              let content = `
                <div>${that.data.date[index]}：</div>
                <div>65+：${that.num.over_sixtyfive[index]}</div>
                <div>60-64：${that.num.sixty_sixtyfour[index]}</div>
                <div>15-59：${that.num.fifteen_fiftynine[index]}</div>
                <div>0-14：${that.num.zero_fourteen[index]}</div>
              `;
              console.log(content); //打印输出信息到控制台
              return content;
            },
          },
          legend: {
            //图例中各数据系列的名称
            data: ["65+", "60-64", "15-59", "0-14"],
            textStyle: {
              //图例文字的样式
              color: "white",
            },
          },
        };
        lineChart.setOption(option); //将定义好的图表配置项 option 应用到指定的图表实例 lineChart 渲染显示
        // 添加点击事件，每次点击后打印该数据点的具体信息到控制台
        lineChart.on('click', function (params) {
          let index = params.dataIndex; //获取当前点击的数据点的索引
          let content = `
       ${that.data.date[index]}：
      65+：${that.num.over_sixtyfive[index]}，
      60-64：${that.num.sixty_sixtyfour[index]}，
     15-59：${that.num.fifteen_fiftynine[index]}，
     0-14：${that.num.zero_fourteen[index]}
     `;
          alert(content); //弹出信息
        });
      });
    },
  },

  mounted() {
    this.getLineData();
  },
};
</script>

<style lang="scss" scoped>
.lineStyle {
  width: 100%;
  height: 23rem;
}
</style>