<template>
    <div>
      <h4>历次普查受教育程度人口</h4>
      <div ref="myLink" class="lineStyle"></div>
    </div>
  </template>
  
  <script>
  import axios from "axios";
  import * as echarts from "echarts";
  
  export default {
    data() {
      return {
        myLink: null,
        data: {},
        dataset: {}
      }
    },
    mounted() {
      this.getPieData();
    },
    methods: {
      async getPieData() {
        let res = await axios.get("https://www.fastmock.site/mock/fd2e7424a07c513608ee1f587b8f58fd/api/pie_line");
        this.data = res.data;
        const rawData = this.data;
        this.dataset = {
          source: [["product", "2012", "2013", "2014", "2015", "2016", "2017"]]
        };
        rawData.product.forEach((product) => {
          const dataRow = [product];
          for (let i = 0; i < rawData.data.length; i++) {
            dataRow.push(rawData.data[i][product.replace(" ", "")]);
          }
          this.dataset.source.push(dataRow);
        });
  
        let linkChart = echarts.init(this.$refs.myLink);
        let option = {
          legend: {
            data: this.data.product,
            textStyle: {
              color: "white",
              // fontSize: "0.18rem"
            }
          },
          tooltip: {
            trigger: "axis",
            showContent: false
          },
          dataset: this.dataset,
          xAxis: { type: "category" },
          yAxis: { gridIndex: 0 },
          grid: { top: "55%" },
          series: [
            {
              type: "line",
              smooth: true,
              seriesLayoutBy: "row",
              emphasis: { focus: "series" }
            },
            {
              type: "line",
              smooth: true,
              seriesLayoutBy: "row",
              emphasis: { focus: "series" }
            },
            {
              type: "line",
              smooth: true,
              seriesLayoutBy: "row",
              emphasis: { focus: "series" }
            },
            {
              type: "line",
              smooth: true,
              seriesLayoutBy: "row",
              emphasis: { focus: "series" }
            },
            {
              type: "pie",
              id: "pie",
              radius: "30%",
              center: ["50%", "25%"],
              emphasis: {
                focus: "self"
              },
              label: {
                formatter: "{b}: {@2012} ({d}%)",
                color: "#ff0000"
              },
              encode: {
                itemName: "product",
                value: "2012",
                tooltip: "2012"
              }
            }
          ]
        };
        linkChart.setOption(option);
  
        linkChart.on("updateAxisPointer", (event) => {
          const xAxisInfo = event.axesInfo[0];
          if (xAxisInfo) {
            const dimension = xAxisInfo.value + 1;
            linkChart.setOption({
              series: {
                id: "pie",
                label: {
                  formatter: "{b}: {@[" + dimension + "]} ({d}%)"
                },
                encode: {
                  value: dimension,
                  tooltip: dimension
                }
              }
            });
          }
        });
      }
    }
  };
  </script>
  
  <style scoped >
  .lineStyle {
    width: 100%;
    height:58rem;
  }
  
  
  </style>

  
 