<template>
  <div class="radar-container">
    <h4>历次普查城镇人口比重</h4>
    <div ref="myRadar" class="radarStyle"></div>
  </div>
</template>

<script>
import axios from "axios";
import * as echarts from "echarts";

export default {
  data() {
    return {
      myRadar: null,
      urban_data: [],
      rural_data: [],
      date_data: [],
      date: null,
      data: null
    }
  },

  mounted() {
    this.myRadar = this.$refs.myRadar;
    this.getBarData().then(() => {
      this.setData();
      let radarChart = echarts.init(this.myRadar);
      let option = {
        legend: {
          data: [{ name: '城市', icon: 'circle' },
          { name: '农村', icon: 'circle' }],
          left: '5%',
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            color: 'yellow',
            fontSize: '12px'
          }
        },
        tooltip: {
          confine: true,
          enterable: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        radar: {
          shape: 'circle',
          indicator: this.date_data,
          radius: "60%",
          axisName: {
            color: '#428BD4'
          },
          splitLine: {
            lineStyle: {
              color: '#2F3D4E'
            }
          },
          splitArea: {
            areaStyle: {
              color: '#2F3D4E'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#2F3D4E'
            }
          }
        },
        series: [
          {
            name: '城市 vs 农村',
            type: 'radar',
            data: [
              {
                value: this.urban_data,
                name: '城市',
                itemStyle: {
                  color: '#ff9f7f'
                },
                lineStyle: {
                  color: '#ff9f7f'
                },
                areaStyle: {
                  color: '#ff9f7f'
                }
              },
              {
                value: this.rural_data,
                name: '农村',
                itemStyle: {
                  color: '#37a2da'
                },
                lineStyle: {
                  color: '#37a2da'
                },
                areaStyle: {
                  color: '#37a2da'
                }
              }
            ]
          }
        ]
      };
      radarChart.setOption(option);
    });
  },

  methods: {
    async getBarData() {
      let radarChart = echarts.init(this.myRadar);
      radarChart.showLoading();  // 显示加载动画
      let Data = await axios({ url: "https://www.fastmock.site/mock/fd2e7424a07c513608ee1f587b8f58fd/api/radar" });
      this.data = Data.data.radar;
      this.date = Data.data.date;
      radarChart.hideLoading();  // 隐藏加载动画
    },

    setData() {
      this.urban_data = this.data.map((v) => v.urban);  
      this.rural_data = this.data.map((v) => v.rural); 
      this.date_data = this.date.map(year => ({ name: year }));  
    }
  }
}
</script>

<style scoped>
.radarStyle {
  width: 100%;
  height: 25rem;
}
</style>