<template>
  <div>
    <h4>历次普查人口性别构成</h4>
    <div class="mixChartStyle" ref="myMix"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import axios from 'axios'

export default {
  data() {
    return {
      myMix: null,
      cate_data: [],
      male_data: [],
      female_data: [],
      sex_data: [],
      barData: {}
    }
  },
  mounted() {
    this.getBarData().then(() => {
      this.setData()
      this.myMix = this.$refs.myMix // 获取 DOM 元素
      const mixChart = echarts.init(this.myMix)
      let option = {}
      option = {
        tooltip: {
          show: true,
          backgroundColor: '#2F3D4E',
          borderColor: '#ddd',
          borderWidth: 1,
          textStyle: {
            color: '#fff'
          }
        },
        legend: {
          data: ['男', '女', '性别比'],
          textStyle: {
            color: 'white',
            fontSize: '12px'
          },
          top: '5%'
        },
        xAxis: {
          type: 'category',
          data: this.cate_data,
          axisLabel: {
            color: 'white',
            fontSize: '12px'
          },
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, .2)'
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              color: 'white',
              fontSize: '12px'
            },
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: 'rgba(255, 255, 255, .2)'
              }
            }
          },
          {
            type: 'value',
            min: 100,
            max: 120,
            interval: 4,
            position: 'right',
            axisLabel: {
              color: 'white',
              fontSize: '12px'
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            }
          }
        ],
        grid: {
          top: '18%',
          left: '5%',
          right: '5%',
          bottom: '8%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {
              iconStyle: {
                borderColor: '#fff'
              }
            }
          }
        },
        series: [
          {
            type: 'bar',
            name: '男',
            data: this.male_data,
            itemStyle: {
              color: new echarts.graphic.LinearGradient(
                0,
                0,
                0,
                1,
                [
                  { offset: 0, color: '#4E8AD8' },
                  { offset: 1, color: '#1B69A9' }
                ]
              ),
              barBorderRadius: 10
            },
            barWidth: 10,
            barGap: '0', // 修改该行
            label: { show: false, position: 'top', color: '#fff' },
            emphasis: { focus: 'series' }
          },
          {
            type: 'bar',
            name: '女',
            data: this.female_data,
            itemStyle: {
              color: new echarts.graphic.LinearGradient(
                0,
                0,
                0,
                1,
                [
                  { offset: 0, color: '#F28B8E' },
                  { offset: 1, color: '#DF686C' }
                ]
              ),
              barBorderRadius: 10
            },
            barWidth: 10,
            barGap: '0', // 修改该行
            label: { show: false, position: 'top', color: '#fff' },
            emphasis: { focus: 'series' }
          },
          {
            type: 'line',
            name: '性别比',
            yAxisIndex: 1, // 修改该行
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    { offset: 0, color: '#FADE5B' },
                    { offset: 1, color: '#F9AC00' }
                  ]
                ),
                borderWidth: 2,
                borderColor: '#fff',
                shadowColor: 'rgba(0, 0, 0, .5)',
                shadowBlur: 5,
                shadowOffsetY: 2
              }
            },
            lineStyle: {
              width: 2,
              shadowColor: 'rgba(15, 21, 77, .5)',
              shadowBlur: 8,
              shadowOffsetY: 4
            },
            symbol: 'circle',
            symbolSize: 6,
            data: this.sex_data,
            markLine: {
              silent: false,
              lineStyle: {
                color: '#e23838',
                type: 'dashed',
                width: 1
              },
              data: [
                { yAxis: 100 },
                { yAxis: 104 },
                { yAxis: 108 },
                { yAxis: 112 },
                { yAxis: 116 },
                { yAxis: 120 }
              ]
            }
          }
        ]
      }
      mixChart.setOption(option)
    })
  },
  methods: {
    async getBarData() {
      let Data = await axios({
        url:
          'https://www.fastmock.site/mock/fd2e7424a07c513608ee1f587b8f58fd/api/line_bar'
      })
      this.sex_data = Data.data.line
      this.barData = Data.data.bar
    },
    setData() {
      this.cate_data = this.barData.map((v) => v.title)
      this.male_data = this.barData.map((v) => v.male)
      this.female_data = this.barData.map((v) => v.female)
    }
  }
}
</script>

<style scoped>
.mixChartStyle {
  width: 100%;
  height: 24rem;
}
</style>

